@import '../varibales/varibal.scss';
.home-page{
    .header{
        overflow: hidden;
        position: relative;
        .right-img{
            padding:15px 14px;
            img{
                width: 100%;
            }
            .header-slbody {
                .container {
                    .cortain {
                        border-radius:5px 5px  10px 10px;
                        overflow: hidden;
                    }
                }
            }
            .header-info{
                position: absolute;
                color: #EEE;
                left: 8%;
                top:30%;
                h2{
                    font-family: 'Dancing Script', cursive;
                    font-size: 50px;
                    font-weight: 700;
                    margin: 0;
                    position: relative;
                    animation: header 1s;
                    animation-fill-mode: forwards;
                    opacity: 0;
                    animation-delay: 1s;
                }
                p{
                    font-size: 20px;
                    position: relative;
                    animation: header 1.2s;
                    animation-fill-mode: forwards;
                    opacity: 0;
                    animation-delay: 1.5s;
                }
                a{
                    text-decoration: none;
                    background: $mainColor;
                    color: #FFF;
                    padding: 8px 13px;
                    font-size: 19px;
                    position: relative;
                    animation: header 1.5s;
                    animation-fill-mode: forwards;
                    opacity: 0;
                    animation-delay: 1.4s;
                    transition:0.5s;
                    
                    &:hover{
                        background:#222;
                    }
                }
            }
        }
        .left-img{
            padding:15px 14px;
            img{
                width: 100%;
                border-radius:5px;
                overflow: hidden;
            }
        }
    }
    .services{
        margin: 15px 14px;
        border: 1px solid #e2e2e2;
        border-radius: 5px;
        padding: 30px;
        overflow: hidden;
        .services-item{
            max-width: 256px;
            margin: 0 auto;
            img{
                float: left;
                height: 50px;
                width: 50px;
            }
            .info{
                float:left;
                margin-left:10px;
                color: #222222;
                h3{
                    font-size: 16px;
                    font-weight: 700;
                    text-transform: capitalize;
                    margin:0;
                }
                p{
                    color:#8b8b8b;
                    font-weight: 600;
                    text-transform: capitalize;
                    margin:0;
                    margin-top:8px;
                }
            }
        }
        .col-3:not(:first-child){
            border-left: 2px solid #e3dede;
        }
    }
    .new-products{
        overflow: hidden;
        .new-products-item{
            padding: 0 14px;
            img{
                width: 100%;
                border-radius: 4px;
            }
        }
    }
    .top-categories{
        padding: 30px 14px 15px;
        .section-name{
            border-bottom: 1px solid #e2e2e2;
            margin-bottom: 30px;
            .top-categories-title{
                margin:0;
                font-size: 22px;
                border-bottom:3px solid $mainColor;
                width: 200px;
                padding-bottom: 10px;
                margin-bottom: 0;
            }
        }
    }
    .new-products-two{
        overflow: hidden;
        .new-products-two-item{
            padding: 15px 14px;
            position: relative;
            img{
                width: 100%;
                border-radius: 4px;
            }
            .item-info{
                position:absolute;
                top:50px;
                right:70px;
                h2{
                    font-weight: normal;
                    font-size: 30px;
                    color:#EEE;
                    span{
                        margin-bottom: 2px;
                        font-weight: bold;
                        display: block;
                    }
                }
                a{
                    text-decoration: none;
                    background: $mainColor;
                    color: #FFF;
                    padding: 8px 27px;
                    font-size: 19px;
                    position: relative;
                    transition:0.5s;
                    &:hover{
                        background: #000;
                    }
                }
            }
            .item-two{
                top:50px;
                left:70px;
            }
        }
    }
    .our-products{
        position: relative;
        padding: 15px 14px;
        .section-name{
            margin:0;
            border-bottom: 1px solid #e2e2e2;
            margin-bottom: 30px;
            .top-categories-title{
                font-size: 22px;
                border-bottom:3px solid $mainColor;
                width: 200px;
                padding-bottom: 10px;
                margin-bottom: 0;
            }
        }
    }
    .classic{
        overflow: hidden;
        .classic-item{
            padding: 15px 14px;
            overflow: hidden;
            img{
                width: 100%;
                border-radius: 4px;
            }
            .bottom-img{
                margin-top: 9px;
            }
        }
        .classic-item:last-child,.classic-item:first-child{
            .img-hover{height:100%}
        }
    }
    .recent-news{
        position: relative;
        overflow: hidden;
        padding:15px 14px;
        .section-name{
            border-bottom: 1px solid #e2e2e2;
            margin-bottom: 30px;
            .top-categories-title{
                margin:0;
                font-size: 22px;
                border-bottom:3px solid $mainColor;
                width: 200px;
                padding-bottom: 10px;
                margin-bottom: 0;
            }
        }
    }
    .our-brands{
        overflow: hidden;
        position: relative;
        padding:15px 14px;
        .section-name{
            border-bottom: 1px solid #e2e2e2;
            margin-bottom: 30px;
            .top-categories-title{
                margin:0;
                font-size: 22px;
                border-bottom:3px solid $mainColor;
                width: 200px;
                padding-bottom: 10px;
                margin-bottom: 0;
            }
        }
    }
    /* set slider buttons */
    .title-slbody .buttons .prev,.title-slbody .buttons .next,
    .logo-slbody .buttons .prev,.logo-slbody .buttons .next,
    .product-slbody .buttons .prev,.product-slbody .buttons .next,
    .news-slbody .buttons .prev,.news-slbody .buttons .next{
        top:-63px;
    }
    }
    /* start home page media */
    @media(max-width:767px){
    .home-page{
        .header{
            padding:0;
            .right-img {
                .header-info{
                    top:30px;
                    h2{
                        font-size: 26px;
                    }
                    p{
                        font-size: 14px;
                    }
                    a{
                        padding: 7px 10px;
                        font-size: 14px;
                    }
                }
            }
            .left-img {
                padding:0 14px 15px;
                img{
                    height:auto;
                } 
            }
        }
        .services {
            padding: 20px;
            .col-3:not(:first-child){
                border-left:none;
                margin-top:20px;
            }
        }
        .new-products {
            padding:15px 0 0;
            .new-products-item:not(last-child){
                padding-bottom: 15px;
            }
        }
        .top-categories {
            padding:15px;
            position: relative;
            .section-name{
                margin-bottom: 25px;
                border-bottom:none;
                text-align: center;
                .top-categories-title{
                    border-bottom:none;
                    width: 100%;
                }
                .top-categories-title:after{
                    content:'';
                    width: 220px;
                    background-color:$mainColor;
                    position: absolute;
                    height: 2px;
                    top: 60px;
                    left:27%;
                }
            }
        }
        .new-products-two{
            padding:0;
            .new-products-two-item{
                .item-info{
                    top: -4px;
                    right: 16px;
                    h2{
                        font-size: 20px;
                    }
                    a{
                        padding: 8px 15px;
                        font-size: 14px;
                    }
                }
                .item-two{
                    top: -4px;
                    left: 16px;
                }
            } 
            .first{
                padding:15px 14px 7px;
            }
            .second{
                padding:7px 14px 15px;
            }
        }
        .our-products{
            padding:15px;
            .section-name{
                margin-bottom: 25px;
                border-bottom:none;
                text-align: center;
                .top-categories-title{
                    border-bottom:none;
                    width: 100%;
                    margin:0;
                }
                .top-categories-title:after{
                    content:'';
                    width: 150px;
                    background-color:$mainColor;
                    position: absolute;
                    height: 2px;
                    top: 60px;
                    left:32%;
                }
            }
            .product-slbody .container .cortain .convoy .content img{
                height:auto;
            }
            .product-slbody .container .cortain .convoy .content .data{
                padding:6px;
            }
        }
        .classic{
            padding:7px 0;
            .classic-item{
                margin:0;
                height: auto;
                padding:7px 14px;
                .img-bottom{
                    margin-top: 0;
                }
            }
        }
        .recent-news{
            .section-name{
                margin-bottom: 25px;
                border-bottom:none;
                text-align: center;
                .top-categories-title{
                    border-bottom:none;
                    width: 100%;
                    margin:0;
                }
                .top-categories-title:after{
                    content:'';
                    width: 180px;
                    background-color:$mainColor;
                    position: absolute;
                    height: 2px;
                    top: 60px;
                    left:30%;
                }
            }
        }
        .our-brands{
            .section-name{
                margin-bottom: 25px;
                border-bottom:none;
                text-align: center;
                .top-categories-title{
                    border-bottom:none;
                    width: 100%;
                    margin:0;
                }
                .top-categories-title:after{
                    content:'';
                    width: 160px;
                    background-color:$mainColor;
                    position: absolute;
                    height: 2px;
                    top: 60px;
                    left:31%;
                }
            }
        }
    /* slider button on mobile */
    .title-slbody .buttons .next,.product-slbody .buttons .next,
    .news-slbody .buttons .next{
        font-size: 20px;
    } 
    .title-slbody .buttons .prev,.product-slbody .buttons .prev,
    .news-slbody .buttons .prev{
        right:auto;
        font-size: 20px;
    }
    }
    }
    @media(min-width:768px){
    .home-page{
        .header {
            .header-slbody .container {
                height: 287.7px;
            }
            .left-img img{
                height: 287.7px;
            }
        }
        .services{
            .services-item-one,.services-item-two{
                margin-bottom: 20px;
            }
            .col-3{
                width: 50%;
            }
            .col-3:not(:first-child){
                border-left:none;
            }
            
        }
        .new-products-two{
            .new-products-two-item{
                .item-info{
                    top:0;
                    right:15px;
                    h2{
                        font-size: 20px;
                    }
                    a{
                        padding: 7px 18px;
                        font-size: 16px;
                    }
                }
                .item-two{
                    left:15px;
                }
            }  
        }
        .classic{
            .classic-item{
            }
        }
        .our-brands{
            padding:15px 30px;
        }
    }
    }
    @media(min-width:991px){
    .home-page{
        .header{
            .header-slbody .container {
                height:500px;
            }
            .left-img img{
                height:500px;
            }
        }
        .services{
            margin: 15px 14px;
            .services-item-one,.services-item-two{
                margin-bottom: 0px;
            }
            .col-3{
                width: 25%;
            }
            .col-3:not(:first-child){
                border-left: 2px solid #e3dede;
            }
        }
        .new-products{
            padding: 15px 0;
        }
        .new-products-two{
            padding:0;
            .new-products-two-item{
                .item-info{
                    top: -9px;
                    right: 70px;
                    h2{
                        font-size: 30px;
                    }
                    a{
                        padding: 8px 27px;
                        font-size: 19px;
                    }
                }
            } 
        }
        .our-products{
            padding:15px 14px;
            .section-name{
                .top-categories-title{
                    width:200px;
                    margin: 0;
                }
            }
        }
        .recent-news{
            padding:15px 14px 0 14px;
        }
        .our-brands{
            padding:0 14px 15px;
        }
    }
}
    /* start header info inimation */
    @keyframes header{
    0%{opacity: 0;bottom:-50px;}
    100%{opacity: 1;bottom:0;}
    }
