//grid system width sass
.clear{
    clear:both
}
@for $i from 1 through 12{
    .col-#{$i}{
        width: (100% / (12/$i) );
        float: left;
    }
}
@media(max-width:767px){
    @for $i from 1 through 12{
        .col-#{$i}{
            width:100%;
        }
    }
}
.row{
    display: flex;
    flex-wrap: wrap;
}